import {onMounted, ref} from 'vue';
import InfoService from '../../services/info.service';

export default function useCentersToRegions() {
  const regionList = ref([]);
  const fetchPositions = async (e) => {
    console.log(e);
    let id = null;
    if (e?.target?.value) {
      id = e.target.value;
    }
    const {data} = await InfoService.fetchRegion(id);
    regionList.value = [...data];
  };
  onMounted(async () => {
    await fetchPositions();
  });

  return {
    regionList,
    fetchPositions,
  };
}
