<template>
  <form @submit="onSubmit">
    <div class="grid grid-cols-3 gap-3 w-full lg:w-[85%]">
      <div class="form_wrapper my-3">
        <label class="input_label" for="last_name"> Familiyasi </label>
        <input id="last_name" v-model="last_name" name="last_name" class="base_input" placeholder="Familiyasini kiriting" />
        <span class="error_message">{{ last_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="first_name"> Ismi </label>
        <input id="first_name" v-model="first_name" name="first_name" class="base_input" placeholder="Ismini kiriting" />
        <span class="error_message">{{ first_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="father_name"> Sharifi </label>
        <input id="father_name" v-model="father_name" name="father_name" class="base_input" placeholder="Sharifini kiriting" />
        <span class="error_message">{{ father_nameError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label"> Tug‘ilgan yili va sanasi </label>
        <DateComponent
          v-model="birth_date"
          :options="{
            maxDate: dayjs().subtract(18, 'year').toDate(),
            startDate: dayjs().subtract(30, 'year').toDate(),
          }"
        />
        <span class="error_message">{{ birth_dateError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="position"> Lavozimi </label>
        <SelectBox :disabled="position_disabled" id="position" v-model="position" :options="positionsList" show-by="name" track-by="code" placeholder="Lavozimni tanlang" />
        <span class="error_message">{{ positionError }}</span>
      </div>
      <!-- <div class="form_wrapper my-3">
        <label class="input_label" for="father_name"> Unvoni </label>
        <input id="rank" v-model="rank" name="rank" class="base_input" placeholder="Unvonini kiriting" />
        <span class="error_message">{{ rankError }}</span>
      </div> -->
      <div class="form_wrapper my-3">
        <label class="input_label" for="position"> Markaz - Tuman </label>
        <SelectBox id="markaz_tuman" v-model="markaz_tuman" :options="regionList" show-by="name" track-by="id" placeholder="Tumanni tanlang" />
        <span class="error_message">{{ markaz_tumanError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="login"> Login </label>
        <input id="login" v-model="login" name="login" class="base_input" placeholder="Login" />
        <span class="error_message">{{ loginError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="password"> Parol </label>
        <input id="password" v-model="password" name="password" class="base_input" placeholder="Yangi parol kiriting" />
        <span class="error_message">{{ passwordError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label" for="email"> Email </label>
        <input id="email" v-model="email" name="email" class="base_input" placeholder="Email misol: test@mail.ru" />
        <span class="error_message">{{ emailError }}</span>
      </div>
      <div class="form_wrapper my-3">
        <label class="input_label"> Rasmi </label>
        <FileInput :id="'photo1'" v-model:files="photo" :multiple-files="false" :initial-files="initialUserPhoto">
          <template #icon>
            <div class="flex justify-between shadow rounded w-full">
              <p class="flex items-center text-gray-400 px-3 overflow-hidden overflow-ellipsis base_input border-none">Rasmni yuklang</p>
              <span class="flex px-4 items-center justify-center rounded bg-primary text-white tracking-wide text-sm cursor-pointer"> YUKLASH </span>
            </div>
          </template>
        </FileInput>
        <span class="error_message">{{ photoError }}</span>
      </div>
    </div>
    <button v-if="isEditMode" class="px-3 m-2 py-2 border bg-primary text-white rounded-md" type="submit" :disabled="isSubmitting">Tahrirlashni tugatish</button>
    <button v-else type="submit" :disabled="isSubmitting" class="base_input bg-primary text-white border-primary">Saqlash</button>
  </form>
</template>

<script setup>
import {useField, useForm} from 'vee-validate';
import * as yup from 'yup';
import FileInput from './Common/InputFile/FileInput';
import {defineEmits, defineProps, ref, toRefs, watch} from 'vue';
import usePositions from '../composables/selectbox-list/usePostions';
import useCentersToRegions from '../composables/selectbox-list/useCentersToRegions';
import dayjs from 'dayjs';
import SelectBox from './Common/SelectBox';
import {isLatinLetters, minValidatorMessage, requiredValidatorMessage} from '../utils/validatorMessages';
import DateComponent from './Common/DateComponent';
import {useRoute, useRouter} from 'vue-router';

const MONITORING = 4;
const position_disabled = ref(false);

const {handleSubmit, isSubmitting, resetForm} = useForm();

const emit = defineEmits(['submit']);
const route = useRoute();
const router = useRouter();

const props = defineProps({
  initialFields: {
    type: Object,
    default: function () {
      return null;
    },
  },
  clearFormMode: {
    type: Boolean,
    default: false,
  },
  isEditMode: {
    type: Boolean,
    default: false,
  },
});
const {isEditMode, clearFormMode} = toRefs(props);

watch(
  () => clearFormMode.value,
  (clearFormMode) => {
    if (clearFormMode === true) {
      resetForm();
    }
  }
);

const {positionsList} = usePositions();
const {regionList} = useCentersToRegions();

const initialUserPhoto = ref([]);

function onInvalidSubmit({values, errors, results}) {
  console.log(values); // current form values
  console.log(errors); // a map of field names and their first error message
  console.log(results); // a detailed map of field names and their validation results
}

const onSubmit = handleSubmit(async ({birth_date, position, ...args}) => {
  const sendObj = {
    ...args,
    groups: [position],
    birth_date: dayjs(birth_date).format('YYYY-MM-DD'),
  };
  const formData = new FormData();
  Object.keys(sendObj).forEach((k) => {
    formData.append(k, sendObj[k]);
  });
  emit('submit', formData);
}, onInvalidSubmit);

const {value: first_name, errorMessage: first_nameError} = useField(
  'first_name',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);

const {value: last_name, errorMessage: last_nameError} = useField(
  'last_name',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);

const {value: father_name, errorMessage: father_nameError} = useField(
  'father_name',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);

const {value: position, errorMessage: positionError} = useField(
  'position',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: markaz_tuman, errorMessage: markaz_tumanError} = useField(
  'markaz_tuman',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: birth_date, errorMessage: birth_dateError} = useField('birth_date', yup.string().required(requiredValidatorMessage), {
  initialValue: new Date(1990, 0, 1),
});
const {value: password, errorMessage: passwordError} = useField(
  'password',
  yup
    .string()
    .required(requiredValidatorMessage)
    .min(8, minValidatorMessage({min: 8}))
);
const {value: login, errorMessage: loginError} = useField(
  'login',
  yup
    .string()
    .test(...isLatinLetters())
    .required(requiredValidatorMessage)
);
const {value: email, errorMessage: emailError} = useField('email', yup.string().email("Email shaklida bo'lishi kerak").required(requiredValidatorMessage));

const {value: photo, errorMessage: photoError} = useField('photo', yup.mixed().nullable().required('Rasm yuklanishi shart'), {
  initialValue: null,
});

watch(
  () => markaz_tuman.value,
  () => {
    console.log(markaz_tuman.value);
  }
);

watch(
  () => positionsList.value,
  (positionsList) => {
    if (positionsList.length) {
      position.value = MONITORING;
    }
  },
  {
    immediate: true,
  }
);
watch(
  () => position.value,
  (posVal) => {
    if (posVal !== MONITORING) {
      router.push({
        name: 'add-worker-to-center',
        query: {
          position: position.value,
        },
      });
    }
  }
);
</script>

<style scoped></style>
